<template>
    <div class="page">
        <Header></Header>
        <div class="main">
            <div class="calculate">
                <div class="display">
                    <div>
                        {{output}}
                    </div>
                </div>
                <div class="content">
                    <div class="row">
                        <input type="text" placeholder="输入你的进制数" v-model="input">
                        <select id="type1" v-model="inputType">
                            <option name="2" value="2">2进制</option>
                            <option name="8" value="8">8进制</option>
                            <option name="10" value="10">10进制</option>
                            <option name="16" value="16">16进制</option>
                        </select>
                    </div>
                    <div class="row">
                        <div class="tip">
                            选择要转换的进制类型
                        </div>
                        <select id="type2" v-model="outputType">
                            <option name="2" value="2">2进制</option>
                            <option name="8" value="8">8进制</option>
                            <option name="10" value="10">10进制</option>
                            <option name="16" value="16">16进制</option>
                        </select>
                    </div>
                    <div class="buttons">
                        <button @click="submit">提交</button>
                        <button @click="clear">清除</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Header from "../components/Header";

    export default {
        name: "Calculate",
        data(){
          return {
              input: null,
              output: null,
              inputType: 2,
              outputType: 2,
          }
        },
        methods: {
            clear(){
                this.input = null
                this.output = null
            },
            submit(){
                this.output = this.result
            }
        }
        ,
        computed: {
            result(){
                let decimalism = parseInt(this.input, this.inputType)
                if (isNaN(decimalism))
                    return ''
                return decimalism.toString(this.outputType)
            }
        },
        components: {
            Header
        }
    }
</script>

<style scoped lang="stylus">
    *
        box-sizing border-box

    .page
        height 100%
        background-color #A6D796
        .main
            width 500px
            margin 0 auto
            max-height 100%
            /*background-color #dc3545*/
            padding-top 150px
            .calculate
                width 100%
                margin 0 auto
                background-color #f2f2f2
                height 400px
                border-radius 10px
                padding 0 10px
                box-shadow 0 8px 6px 4px rgba(0, 0, 0, 0.1)
                .display
                    height 30%
                    width 100%
                    padding-top 35px
                    div
                        padding-left 10px
                        height 70px
                        background-color #2a2a2a
                        margin 0 20px
                        border-radius 6px
                        color #fff
                        line-height 70px
                        font-size 36px
                .content
                    text-align center
                    padding-top 30px
                    input
                        width 340px
                        height 55px
                        text-align center
                        border 1px solid #E6E6E6
                        border-right none
                        border-radius 4px 0 0 4px
                        margin-bottom 20px
                        font-size 18px
                        color #777
                        padding 0
                    input::placeholder
                        font-size 16px
                        color #e2e3e5
                    select
                        display inline-block
                        height 55px
                        width 100px
                        border 1px solid #E6E6E6
                        border-left none
                        border-radius 0 4px 4px 0
                        vertical-align top
                        font-size 15px

    .tip
        display inline-block
        vertical-align center
        width 340px
        height 55px
        line-height 55px
        text-align center
        border 1px solid #E6E6E6
        border-right none
        border-radius 4px 0 0 4px
        font-size 15px
        color #555
        background-color #e2e2e2

    #type2
        vertical-align top

    .buttons
        width 440px
        margin 0 auto
        display flex
        justify-content space-between
        margin-top 30px
        button
            width 200px
            height 55px
            border 1px solid #E6E6E6
            background-color #fff
            border-radius 4px
            cursor pointer
</style>
